.entertainment{
    margin: 0px 50px;
    border-top: 1px dotted black;
    padding: 20px 0;
    margin-bottom:20px ;
}
.entertainment__heading {
    font-size: 20px;
    display: flex;
    align-items: center;

}

.heading-arrow {
    margin-left: 4px;
    height: 20px;
    width: 20px;
}

.entertainment__wrapper {
    margin-top: 10px;
    display: flex;
}

.entertainment__left {
    border-right: 1px dotted #a5a5a5;
    /* padding-right: 16px; */
    width: 66.66667%;
    display: flex;
    margin-right: 20px;
}

.entertainment__leftMain {
    width: 50%;
    padding-right: 20px;
}

.entertainment__leftTop {
    position: relative;
    border-radius: 5px;
    overflow: hidden;
}

.entertainment__leftTop img {
    width: 100%;
    object-fit: cover;
    vertical-align: bottom;
    transition: all 1s;
}

.entertainment__leftTop img:hover {

    -webkit-transform: scale(1.1);

            transform: scale(1.1);

}

.entertainment__main-title {
    position: absolute;
    padding: 0 16px 12px;
    bottom: 0;
    left: 0;
    color: #fff;
    font-size: 16px;
    text-shadow: 0 2px 8px rgba(0, 0, 0, .4);
}


.entertainment__leftBottom {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 20px;
    gap: 20px;
    margin-top: 25px;
}

.entertainment__leftBottom-img {
    border-radius: 5px;
    overflow: hidden;
    /* margin-bottom: 5px; */
}

.entertainment__leftBottom-img img {
    width: 100%;
    object-fit: cover;
    vertical-align: bottom;
    transition: all 1s;
}

.entertainment__leftBottom-img img:hover {

    -webkit-transform: scale(1.1);

            transform: scale(1.1);

}


.entertainment__leftBottom--single p {
    font-size: 13px;
}



.entertainment__leftShorts {
    width: 43%;
    padding-left:16px;
}

.entertainment__leftBottom-ul{
    list-style: none;
    padding: 0;
    margin: 0;

}
.entertainment__leftBottom-ul li {
    padding: 8px 0;
    /* margin-left: 15px; */
    border-bottom: 1px dotted #a5a5a5;
}

.entertainment__leftBottom-ul span {
    font-size: 13px;
}




.entertainment__right {
    padding-left: 10;
    max-width: 33.33333%;
}

.entertainment__right-h2 {
    font-size: 14px;
    display: flex;
    align-items: center;
}



.entertainment__rightImages {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 15px ;
    gap: 15px ;
    margin-top: 10px;
}

.entertainment__rightImages div {
    position: relative;
    overflow: hidden;
    margin-bottom: 5px;


}

.entertainment__rightImg-container img {
    width: 100px;
    height: 100px;
    border-radius: 5px;
    object-fit: cover;
    vertical-align: bottom;
    transition: all 1s;
}

.entertainment__rightImg-container img:hover {

    -webkit-transform: scale(1.1);

            transform: scale(1.1);

}

.entertainment__rightImg-container p {
    font-size: 13px;
}
